import navigation from '../../components/navigation/navigation';
export default {
	data() {
		return {
			config: {
				type: 1, //0无title，1有title
				title: "我的",
				back: false
			},
			code: '',
			userId: '',
			avatar: '',
			name: '',
			firstTime: true,
			msgList: [{
				type: '0',
				msg: '你的card有人申请了!',
				cardId: '1'
			}, {
				type: '1',
				msg: '你的队伍有人申请加入了!',
				cardId: '1'
			}, {
				type: '2',
				msg: '你的入队申请通过了!',
				cardId: '1'
			}, {
				type: '3',
				msg: '你的队伍有人退出了!',
				cardId: '1'
			}, {
				type: '4',
				msg: '你的队伍解散了!',
				cardId: '1'
			}, {
				type: '5',
				msg: '你的card有人申请了!',
				cardId: '1'
			}]
		}
	},
	components: {
		navigation
	},
	onReady() {
		if (uni.getStorageSync('userId')&&uni.getStorageSync('token')) {
			this.userId = uni.getStorageSync('userId')
			this.getuser()
		} else {
		let _this = this
		console.log(1)
		uni.login({
			provider: 'weixin',
			success: function(response) {
				console.log(response)
				_this.code = response.code
			},
			fail: (err) => {
				console.log(err)
			}
		})
		}
	},
	methods: {
		goEdit: function() {
			// wx.navigateTo({
			//   url: '../my/editMine'　
			// })
		},
		goFinish: function() {
			// wx.navigateTo({
			//   url: '../my/finish'　
			// })
		},
		goUnfinish: function() {
			// wx.navigateTo({
			//   url: '../my/unfinish'　
			// })
		},
		//获取微信头像昵称
		getuserinfo(e) {
			console.log(2)
			console.log(e.detail.userInfo);
			let user = e.detail.userInfo;
			this.name = user.nickName;
			this.avatar = user.avatarUrl;
			let url = 'http://188.131.227.20:1314/api/login/'
			let _this = this
			uni.request({
				url: url,
				method: 'POST',
				data: {
					code: _this.code
				},
				success: res => {
					console.log(res.data);
					_this.userId = res.data.userId
					_this.firstTime = res.data.firstTime
					uni.setStorageSync('token', res.data.token)
					uni.setStorageSync('userId', res.data.userId)
					if (!res.data.firstTime) {
						_this.getuser()
					} else {
						_this.sendfirst()
					}
					uni.setStorageSync('avatar', _this.avatar)
					uni.setStorageSync('name', _this.name)
				},
				fail: err => {
			
				}
			})
		},
		//获取数据库中头像昵称
		getuser() {
			this.$http.get('/api/person/info/', {
				userId: this.userId
			}).then(res => {
				if (res.result == 1) {
					this.name = res.name;
					this.avatar = res.avatarUrl;
				}
			}).catch((e) => {});
		},
		sendfirst() {
			this.$http.post('/api/person/update/', {
				userId: this.userId,
				name: this.name,
				sex: null,
				introduce: null,
				avatarUrl: this.avatar,
			}).then(res => {
				uni.showToast({
					title: '登录成功',
					icon: 'none'
				})
			}).catch((e) => {});
		}
	}
}
